<template>
  <div class="mySubscribeBox">
    <div class="mySubscribe" @click="$parent.bg = 'registrationForm'">
      新增申请
    </div>
    <div
      class="ItemContent"
      v-for="item in volunteerList"
      :key="item.uuid"
      @click="gotoDetailsForm(item)"
    >
      <ul>
        <li class="ICTime">{{ item.name }}</li>
        <li>{{ item.sex }}</li>
        <li>{{ item.phoneNumber }}</li>
        <li>
          <icon
            :name="getIconName(item.checkState)"
            :color="getIconColor(item.checkState)"
            size="5vw"
          />
          <div :style="{ color: getIconColor(item.checkState) }">
            {{ getIconText(item.checkState) }}
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { getVolunteerList } from "@/api/data/reservation";
import { Icon } from "vant";
export default {
  components: {
    Icon,
  },
  data() {
    return {
      volunteerList: [],
    };
  },
  methods: {
    gotoDetailsForm(item) {
      this.$parent.bg = "detailsForm";
      this.$nextTick(() => {
        this.$bus.$emit("dataChild", item);
      });
    },
    getVolunteerList_() {
      getVolunteerList().then((res) => {
        this.volunteerList = res.data;
      });
    },
    getIconName(val) {
      let name = "passed";
      if (val == 1 || val == null) {
        name = "more-o";
      } else if (val == 2) {
        name = "passed";
      } else {
        name = "close";
      }
      return name;
    },
    getIconColor(val) {
      let color = "#15CFA4";
      if (val == 1 || val == null) {
        color = "#FF900A";
      } else if (val == 2) {
        color = "#15CFA4";
      } else {
        color = "#ff5d00";
      }
      return color;
    },
    getIconText(val) {
      let text = "申请成功";
      if (val == 1 || val == null) {
        text = "处理中";
      } else if (val == 2) {
        text = "申请成功";
      } else {
        text = "申请失败";
      }
      return text;
    },
  },
  created() {
    this.getVolunteerList_();
  },
};
</script>
<style scoped lang="scss">
.mySubscribeBox {
  position: relative;
  padding-top: 8vw;
  .mySubscribe {
    position: absolute;
    top: 1vh;
    right: 2vw;
    background: #15cfa400;
    border-radius: 0.5vw;
    border: 1px solid #003f30;
    padding: 0.5vh 2vw;
    font-size: 4vw;
    color: #008466;
    line-height: 4vw;
  }
  .ItemContent {
    color: #868786;
    font-size: 2.48vh;
    font-weight: 400;
    padding: 0 1vw;
    ul {
      height: 7vw;
      line-height: 7vw;
      background-color: #f9fffe;
      margin: 2vw 0;
      border-radius: 1vw;
      list-style: none;
      display: flex;
      padding: 3vw 1vw;
      li {
        width: 25%;
        margin-left: 2vw;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      li:nth-child(1) {
      }
      li:nth-child(2) {
        width: 5%;
      }
      li:nth-child(3) {
        width: 34%;
      }
      li:nth-child(4) {
        width: 30%;
        display: flex;
        align-items: center;

        div {
          margin-left: 1vw;
        }
      }
    }
  }
  .mask {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.404);
    z-index: 15;
    .MessageBox {
      width: 77vw;
      height: 25vh;
      background-color: white;
      margin: 30vh auto;
      .MessageTop {
        color: white;
        display: flex;
        justify-content: center;
        height: 6vh;
        line-height: 6vh;
        background-color: #15cfa4;
        position: relative;
        img {
          position: absolute;
          top: 2vw;
          right: 1vw;
          width: 6vw;
          height: 6vw;
        }
      }
      .MessageBom {
        margin: 7vw 0 0 10vw;
        font-weight: 900;
      }
    }
  }
}
</style>