<template>
  <div class="ItemBox">
    <div class="mySubscribe" @click="$parent.bg = 'mySubscribe'">我的申请</div>
    <div class="appHomeTitBox">
      <div class="titleName">|&nbsp;&nbsp;保护区愿者报名登记表</div>
    </div>
    <div class="appHomeContent wrap">
      <div class="contentItem">
        <div class="itemLabel">
          |&nbsp;&nbsp; <span class="redSpot"></span>姓名
        </div>

        <div class="itemValue w77">
          <input
            type="text"
            placeholder="请输入姓名"
            v-model="informationData.name"
          />
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">
          |&nbsp;&nbsp; <span class="redSpot"></span>性别
        </div>

        <div class="itemValue w77">
          <select v-model="informationData.sex" placeholder="请选择">
            <option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></option>
          </select>
          <!-- <input type="text" v-model="informationData.sex" /> -->
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">
          |&nbsp;&nbsp; <span class="redSpot"></span>年龄
        </div>

        <div class="itemValue w77">
          <input
            type="text"
            placeholder="请输入年龄"
            v-model="informationData.age"
          />
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">
          |&nbsp;&nbsp; <span class="redSpot"></span>文化程度
        </div>

        <div class="itemValue w77">
          <select v-model="informationData.educationDegree">
            <option
              v-for="item in degree_education"
              :key="item.value"
              :label="item.dictLabel"
              :value="item.dictValue"
            ></option>
          </select>
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">
          |&nbsp;&nbsp; <span class="redSpot"></span>职务/职称
        </div>

        <div class="itemValue w77">
          <input
            type="text"
            placeholder="请输入职称"
            v-model="informationData.positionalTitles"
          />
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">
          |&nbsp;&nbsp; <span class="redSpot"></span>联系电话
        </div>

        <div class="itemValue w77">
          <input
            type="text"
            placeholder="请输入电话"
            v-model="informationData.phoneNumber"
          />
        </div>
      </div>
    </div>
    <div class="appHomeContent">
      <div class="contentItem">
        <div class="itemLabel">
          |&nbsp;&nbsp; <span class="redSpot"></span>工作单位
        </div>

        <div class="itemValue w77">
          <input
            type="text"
            placeholder="请输入工作单位"
            v-model="informationData.workUnit"
          />
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">
          |&nbsp;&nbsp; <span class="redSpot"></span>家庭住址
        </div>

        <div class="itemValue w77">
          <input
            type="text"
            placeholder="请输入家庭住址"
            v-model="informationData.homeAddress"
          />
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">
          |&nbsp;&nbsp; <span class="redSpot"></span>业务特长
        </div>

        <div class="itemValue W0">
          <textarea
            name
            id
            v-model="informationData.businessExpertise"
          ></textarea>
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">|&nbsp;&nbsp;备注</div>
        <div class="itemValue W0">
          <textarea name id v-model="informationData.remark"></textarea>
        </div>
      </div>
    </div>
    <button class="Btn" @click="BtnAdd">提交</button>
  </div>
</template>
<script>
import { addVolunteer } from "@/api/data/reservation";
import { positiveIntegerNumber } from "@/utils/validate";
export default {
  data() {
    return {
      options: [
        {
          value: "男",
          label: "男",
        },
        {
          value: "女",
          label: "女",
        },
      ],
      degree_education: [],
      informationData: {
        name: "",
        sex: "男",
        age: "",
        educationDegree: "",
        positionalTitles: "",
        phoneNumber: "",
        workUnit: "",
        homeAddress: "",
        businessExpertise: "",
        remark: "",
        checkState: "1",
      },
      informationDataRules: {
        name: "姓名",
        sex: "性别",
        age: "年龄",
        educationDegree: "文化程度",
        positionalTitles: "职务/职称",
        phoneNumber: "联系电话",
        workUnit: "工作单位",
        homeAddress: "家庭住址",
        businessExpertise: "业务特长",
      },
    };
  },
  methods: {
    BtnAdd() {
      let val = this.$deepCopy(this.informationData);
      let flag = true;
      for (let item in this.informationDataRules) {
        if (val[item] == "" || val[item] == null || val[item] == undefined) {
          flag = false;
          this.$message.warning(this.informationDataRules[item] + "未填写!");
          return;
        }
      }
      if (flag) {
        if (positiveIntegerNumber(val.age).flag) {
          let phone =
            /^(?:(?:\+|00)86)?1(?:(?:3[\d])|(?:4[5-79])|(?:5[0-35-9])|(?:6[5-7])|(?:7[0-8])|(?:8[\d])|(?:9[189]))\d{8}$/;
          if (phone.test(val.phoneNumber)) {
            addVolunteer(val).then((res) => {
              this.$message.success(res.msg);
              this.informationData = this.$options.data().informationData;
              this.informationData.educationDegree =
                this.degree_education[
                  this.degree_education.length - 1
                ].dictValue;
            });
          } else {
            this.$message.warning("请输入正确手机号!");
          }
        } else {
          this.$message.warning("请输入年龄!");
        }
      }
    },
  },
  created: async function () {
    await this.dictType("degree_education").then((res) => {
      this.degree_education = res;
      this.informationData.educationDegree = res[res.length - 1].dictValue;
    });
  },
  mounted() {},
};
</script>
<style scoped lang="scss">
.ItemBox {
  width: 95%;
  margin: 0 auto;
  background-color: white;
  position: relative;
  .mySubscribe {
    position: absolute;
    top: 1vh;
    right: 2vw;
    background: #15cfa4;
    border-radius: 0.5vw;
    padding: 0.5vh 2vw;
    font-size: 4vw;
    color: #fff;
    line-height: 4vw;
  }
  .appHomeTitBox {
    height: 6.55vw;
    line-height: 6.55vw;
    margin: 2vw 0 0 2vw;
    font-size: 4vw;
    padding-top: 1vw;
    display: flex;
    justify-content: space-between;
    .titleName {
      font-size: 5vw;
      font-weight: bold;
    }
    .appointment {
      width: 20vw;
      text-align: center;
      border-radius: 0.3vw;
      margin-right: 1vw;
      color: #ffffff;
      background-color: #20cfa4;
    }
  }
  .appHomeContent {
    width: calc(100% - 2vw);
    margin: 2.6vw 0 0 0;
    padding: 0 1vw;
    display: flex;
    flex-wrap: wrap;
    .contentItem {
      width: 100%;
      display: flex;
      margin-bottom: 2.42vw;
      position: relative;
      .itemLabel {
        width: 22vw;
        margin-right: 0.9vw;
        padding-left: 1vw;
        font-size: 3.4vw;
        font-weight: bold;
        color: #008c8a;
        // text-align-last: justify;
      }
      .redSpot {
        position: relative;
        &::after {
          content: "*";
          color: red;
        }
      }
      .itemValue {
        flex: 1;
        background: #f5f8fd;
        font-size: 3.2vw;
        font-family: PingFangSC, PingFangSC-Regular;
        font-weight: 400;
        text-align: left;
        color: #868786;
        padding-left: 1vw;
        input {
          width: 100%;
          border: none;
          padding: 0.5vw 0;
          background: #f5f8fd;
        }
        input::placeholder {
          font-size: 2.66vw;
        }
        textarea {
          width: 100%;
          height: 100%;
          background: #f5f8fd;
          border: none;
          resize: none;
        }
        select {
          width: 100%;
          background: #f5f8fd;
          border: none;
        }
      }
      .w77 {
        height: 4.4vw;
        line-height: 4.4vw;
        max-width: 77vw;
      }
      .W0 {
        max-width: 77vw;
        height: 20vw;
      }
    }
  }
  .wrap {
    .contentItem {
      width: 50%;
    }
  }
  .Btn {
    text-align: center;
    height: 6vh;
    line-height: 6vh;
    margin: 6vw 0 0 3vw;
    width: 90vw;
    font-size: 4vw;
    border: #15cfa4 0.4vw solid;
    background-color: #15cfa4;
    color: white;
  }
}
</style>