<template>
  <div class="ItemBox">
    <div class="mySubscribe" @click="$parent.bg = 'registrationForm'">
      新增申请
    </div>
    <div class="appHomeTitBox noEdit">
      <div class="titleName">|&nbsp;&nbsp;基本信息</div>
    </div>
    <div class="appHomeContent wrap noEdit">
      <div class="contentItem">
        <div class="itemLabel">|&nbsp;&nbsp;姓名</div>
        <div class="redSpot"></div>
        <div class="itemValue w77">
          <input
            type="text"
            placeholder="请输入姓名"
            v-model="informationData.name"
          />
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">|&nbsp;&nbsp;性别</div>
        <div class="redSpot"></div>
        <div class="itemValue w77">
          <select v-model="informationData.sex" placeholder="请选择">
            <option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></option>
          </select>
          <!-- <input type="text" v-model="informationData.sex" /> -->
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">|&nbsp;&nbsp;年龄</div>
        <div class="redSpot"></div>
        <div class="itemValue w77">
          <input
            type="text"
            placeholder="请输入年龄"
            v-model="informationData.age"
          />
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">|&nbsp;&nbsp;文化程度</div>
        <div class="redSpot"></div>
        <div class="itemValue w77">
          <select v-model="informationData.educationDegree">
            <option
              v-for="item in degree_education"
              :key="item.value"
              :label="item.dictLabel"
              :value="item.dictValue"
            ></option>
          </select>
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">|&nbsp;&nbsp;职务/职称</div>
        <div class="redSpot"></div>
        <div class="itemValue w77">
          <input
            type="text"
            placeholder="请输入职称"
            v-model="informationData.positionalTitles"
          />
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">|&nbsp;&nbsp;联系电话</div>
        <div class="redSpot"></div>
        <div class="itemValue w77">
          <input
            type="text"
            placeholder="请输入电话"
            v-model="informationData.phoneNumber"
          />
        </div>
      </div>
    </div>
    <div class="appHomeContent noEdit">
      <div class="contentItem">
        <div class="itemLabel">|&nbsp;&nbsp;工作单位</div>
        <div class="redSpot"></div>
        <div class="itemValue w77">
          <input
            type="text"
            placeholder="请输入工作单位"
            v-model="informationData.workUnit"
          />
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">|&nbsp;&nbsp;家庭住址</div>
        <div class="redSpot"></div>
        <div class="itemValue w77">
          <input
            type="text"
            placeholder="请输入家庭住址"
            v-model="informationData.homeAddress"
          />
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">|&nbsp;&nbsp;业务特长</div>
        <div class="redSpot"></div>
        <div class="itemValue W0">
          <textarea
            name
            id
            v-model="informationData.businessExpertise"
          ></textarea>
        </div>
      </div>
      <div class="contentItem">
        <div class="itemLabel">|&nbsp;&nbsp;备注</div>
        <div class="itemValue W0">
          <textarea name id v-model="informationData.remark"></textarea>
        </div>
      </div>
    </div>
    <div
      class="Btn noEdit"
      :style="{ background: getBtnColor(informationData.checkState) }"
    >
      {{ "您的申请" + getBtnText(informationData.checkState) }}
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      options: [
        {
          value: "男",
          label: "男",
        },
        {
          value: "女",
          label: "女",
        },
      ],
      degree_education: [],
      informationData: {
        name: "",
        sex: "男",
        age: "",
        educationDegree: "",
        positionalTitles: "",
        phoneNumber: "",
        workUnit: "",
        homeAddress: "",
        businessExpertise: "",
        remark: "",
        checkState: "1",
      },
    };
  },
  methods: {
    getBtnText(val) {
      let text = "通过";
      if (val == 1) {
        text = "正在审批中";
      } else if (val == 2) {
        text = "审批通过";
      } else {
        text = "审批不通过";
      }
      return text;
    },
    getBtnColor(val) {
      let color = "#15CFA4";
      if (val == 1) {
        color = "#FF900A";
      } else if (val == 2) {
        color = "#15CFA4";
      } else {
        color = "#ff5d00";
      }
      return color;
    },
  },
  created: async function () {
    await this.dictType("degree_education").then((res) => {
      this.degree_education = res;
    });
  },
  mounted: async function () {
    let _this = this;
    this.$bus.$on("dataChild", (details) => {
      _this.informationData = details;
    });
  },
  beforeDestroy: function () {
    this.$bus.$off("dataChild");
  },
};
</script>
<style scoped lang="scss">
.ItemBox {
  width: 95%;
  margin: 0 auto;
  background-color: white;
  position: relative;
  .noEdit {
    pointer-events: none;
  }
  .mySubscribe {
    position: absolute;
    top: 1vh;
    right: 2vw;
    background: #15cfa400;
    border-radius: 0.5vw;
    border: 1px solid #003f30;
    padding: 0.5vh 2vw;
    font-size: 4vw;
    color: #008466;
    line-height: 4vw;
  }
  .appHomeTitBox {
    height: 6.55vw;
    line-height: 6.55vw;
    margin: 2vw 0 0 2vw;
    font-size: 4vw;
    padding-top: 1vw;
    display: flex;
    justify-content: space-between;
    .titleName {
      font-size: 5vw;
      font-weight: bold;
    }
    .appointment {
      width: 20vw;
      text-align: center;
      border-radius: 0.3vw;
      margin-right: 1vw;
      color: #ffffff;
      background-color: #20cfa4;
    }
  }
  .appHomeContent {
    width: calc(100% - 2vw);
    margin: 2.6vw 0 0 0;
    padding: 0 1vw;
    display: flex;
    flex-wrap: wrap;
    .contentItem {
      width: 100%;
      display: flex;
      margin-bottom: 2.42vw;
      position: relative;
      .itemLabel {
        width: 22;
        margin-right: 0.9vw;
        padding-left: 1vw;
        font-size: 3.8vw;
        font-weight: bold;
        color: #008c8a;
        // text-align-last: justify;
      }
      .redSpot {
        position: absolute;
        top: 2vw;
        font-size: 15vw;
        border-radius: 100%;
        // background-color: red;
        width: 1.2vw;
        height: 1.2vw;
      }
      .itemValue {
        flex: 1;
        background: #fff;
        font-size: 3.2vw;
        font-family: PingFangSC, PingFangSC-Regular;
        font-weight: 400;
        text-align: left;
        color: #868786;
        padding-left: 1vw;
        input {
          width: 100%;
          border: none;
          padding: 0.5vw 0;
          background: #fff;
        }
        input::placeholder {
          font-size: 2.66vw;
        }
        textarea {
          width: 100%;
          height: 100%;
          background: #fff;
          border: none;
          resize: none;
        }
        select {
          width: 100%;
          background: #fff;
          color: #868786;
          border: none;
        }
      }
      .w77 {
        height: 4.4vw;
        line-height: 4.4vw;
        max-width: 77vw;
      }
      .W0 {
        max-width: 77vw;
        height: 20vw;
      }
    }
  }
  .wrap {
    .contentItem {
      width: 50%;
    }
  }
  .Btn {
    text-align: center;
    height: 6vh;
    line-height: 6vh;
    margin: 6vw 0 0 3vw;
    width: 90vw;
    font-size: 4vw;
    // border: #15cfa4 0.4vw solid;
    background-color: #15cfa4;
    color: white;
  }
}
</style>